const InsertionSort = (array, position, steps, colors) => {

    let colorKey = colors[colors.length - 1].slice();
    let key, j, i;
    for (i = 1; i < array.length; i++) {
        key = array[i]
        j = i - 1
        while (j >= 0 && array[j] > key)
        {
            array[j + 1] = array[j];
            steps.push(array.slice());
            colorKey[i] = 2;

            if ( i === j + 1 ){
                colorKey[j + 1] = 2;
            } else{
                colorKey[j + 1] = 1;
            }
            // colorKey[j + 1] = 1;
            colorKey[j] = 1;
            colors.push(colorKey.slice());
            colorKey[j + 1] = 0;
            colorKey[j] = 0;
            colorKey[i] = 0;
            // colorKey[j + 1] = 0;

            j = j - 1;
        }
        array[j + 1] = key;
        steps.push(array.slice());
        colors.push(colorKey.slice());
	}
    colors[colors.length - 1] = new Array(array.length).fill(2);

};



export default InsertionSort;
